<template>
   <div class="container-fluid nav-fixed">
       <div class="row">
           <div class="col-md-3">
               <div class="logo">
                   <div class="logo-cont">
                       <img src="../assets/images/retailpay_logo.png" :class="windowWidth <=767 ? 'mt-1' : 'mt-3'" class="main-nav-logo" alt="Retailpay">
                   </div>

                    <div class="toggler" v-if="windowWidth <= 767">
                        <i @click="sidenav = true" class="fa fa-bars fa-2x"></i>
                    </div>

               </div>
           </div>
           <div class="col-md-9" v-if="sidenavs.length > 0">
               <div class="nav" v-if="windowWidth > 767">
                   <router-link v-for="sidenav in sidenavs" :key="sidenav.id" class="link-item" to="#">
                       <button> {{ sidenav.name }} </button>
                   </router-link>
               </div>

               <!-- Responsive Nav -->
               <div class="container-fluid resp-nav-cont p-0 p-10  bg-dark text-white" v-if="sidenav">
                   <div class="row row-c resp-nav-cont-top">
                       <div class="col-12"> 
                           <div class="row">
                               <div class="col-8 logo-cont">
                                   <img src="../assets/images/retailpay_logo.png" class="mt-1" alt="Retailpay">
                               </div>
                               <div class="col-4">
                                   <i @click="sidenav = false" class="fa fa-times fa-2x"></i>
                               </div>
                           </div>

                           <div class="row pt-2 pb-2">
                               <div class="col-12">
                                   <hr>
                               </div>
                           </div>
                       </div>
                   </div>


                   <div class="row bg-white">
                       <div class="col-12">
                           <div class="nav-r">
                                <router-link class="link-item-r" to="#">
                                    <button @click="sidenav = false">SUPPLIERS</button>
                                </router-link>
                                <router-link class="link-item-r" to="#">
                                    <button @click="sidenav = false">RETAILERS</button>
                                </router-link>
                                <router-link class="link-item-r" to="#">
                                    <button @click="sidenav = false">DISTRIBUTORS</button>
                                </router-link>
                                <router-link class="link-item-r" to="#">
                                    <button @click="sidenav = false">ABOUT US</button>
                                </router-link>
                                <router-link class="link-item-r" to="/">
                                    <button @click="sidenav = false">HELP CENTER</button>
                                </router-link>
                                <router-link class="link-item-r" to="#">
                                    <button @click="sidenav = false"> {{ windowWidth }} </button>
                                </router-link>
                            </div>
                       </div>
                   </div>

                   <div class="row">
                       <div class="col-12 terms bg-white pb-5">
                           <hr>

                           <div>
                               <router-link style="display: inline-block" to="#">TERMS AND CONDITIONS</router-link>
                           </div>
                       </div>
                   </div>
               </div>

           </div>
       </div>
   </div>
</template>

<script>
import axios from 'axios'
import { Config } from "../config"
export default {
    data() {
        return {
             windowWidth: 0,
             sidenav: false,
             sidenavs: []
        }
    },
    created() {
        axios.get(`${ Config.rootUrl }/mainnavs`)
        .then((resp) => {
            this.sidenavs = resp.data
        })
        .catch(err => alert(err))
    },
    mounted() {
    this.$nextTick(function() {
      window.addEventListener('resize', this.getWindowWidth);
      
      //Init
      this.getWindowWidth()
    })
  },
  methods: {
    getWindowWidth(event) {
        this.windowWidth = document.documentElement.clientWidth;
    }
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.getWindowWidth);
  }
}
</script>

<style scoped>
    .nav {
        height: 50px;
        width: 90%;
        display: flex;
        flex-direction: row;
        padding-top: 15px;
        padding-bottom: 60px !important;
    }

    .link-item {
        flex: 1;
    }

    .link-item button {
        width: 100%;
        height: 50px;
        background: transparent;
        border: none;
        outline: none;
        color: gray;
        transition-duration: .5s;
    }

    .link-item button:hover {
        color: rgb(23,99,173);
        font-weight:bold;
    }

    .resp-nav-cont {
        position: fixed;
        z-index: 1;
        top: 0;
        right: 0;
        bottom: 0;
    }

    .resp-nav-cont hr {
        width: 80%;
        float: left;
    }

    .p-10 {
        padding-left: 100px !important;
    }

    .resp-nav-cont-top {
        background: white;
        height: auto;
        color: rgb(23,99,173);
        padding-top: 15px;
    }

    .logo-cont {
        padding-left: 20px;
    }

    .nav-r {
        /* width: 90%; */
        display: flex;
        flex-direction: column;
    }

    .link-item-r {
        flex: 1;
        background: white;
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .link-item-r button {
        width: 100%;
        height: auto;
        padding-top: 7px;
        padding-bottom: 7px;
        background: transparent;
        border: none;
        outline: none;
        color: gray;
        transition-duration: .5s;
        text-align: left;
    }

    .link-item-r button:hover {
        color: rgb(23,99,173);
        font-weight:bold;
    }

    .fa-times:hover {
        cursor: pointer;
        font-weight: lighter;
    }

    .nav-fixed {
        position: fixed;
        z-index: 1;
        background: white;
    }

    @media screen and (max-width: 767px) {

        .logo {
            display: flex !important;
            flex-direction: row !important;
        }

        .logo div {
            flex: 1;
        }

        .logo-cont {
            padding-top: 8px;
        }
        .logo-cont img {
            /* background: red;
            height: 50px; */
        }

        .toggler {
            padding-top: 10px;
            padding-bottom: 10px;
            padding-right: 20px;
        }

        .toggler i {
            float: right;
            cursor: pointer;
        }
    }
</style>