<template>
    <div class="container-fluid">
        <div class="row">

            <!-- Display when the side navbar id no available-->
                <button @click="show_side_nav = true" class="arrow-cont arrow-cont-open"  :class="show_side_nav ? 'hide' : 'arrow-cont-open'" title="Click For More">
                    <i class="fa fa-arrow-right"></i>
                </button>


            <!-- Side navigation -->
            <div class="col-md-3 p-0 side-nav-cont" :class="show_side_nav ? 'side-nav-cont-show' : 'close-sidenav'" id="side-nav-cont">
                <button @click="show_side_nav = false" class="arrow-cont arrow-cont-close">
                    <i class="fa fa-arrow-left"></i>
                </button>


                <!-- Loader -->
                <div class="container-fluid" v-if="page_object.sidenavs.length == 0">

                    <div class="row">
                        <div class="col-12">
                            <div class="loader">
                                <img class="img-fluid" :src="loader_url" alt="">
                            </div>
                        </div>
                    </div>

                    <div class="row">
                        <div class="col-12 text-center">
                            <h6>Please Wait. Loading Page Navigation....</h6>
                        </div>
                    </div>

                </div>
                <!-- End of loader -->


                <ul v-if="page_object.sidenavs.length > 0" class="list-group" :class="show_side_nav ? 'animated slideInLeft' : 'not-important'">
                    <li v-for="sidenav in page_object.sidenavs" :key="sidenav.id" @click="updateCurrentObject(sidenav)" class="list-group-item">
                        <h6 class="p-size">
                            <div class="current-indicate" v-if="sidenav.id == current_sidenav_id"></div>
                            {{ sidenav.name }} 
                            <span v-if="sidenav.subheadings.length > 0">
                                <!-- <button class="drop-subheadings-btn"  @click="current_sidenav_id = sidenav.id"> -->
                                    <i :class="sidenav.id == current_sidenav_id ? 'fa fa-angle-down' : 'fa fa-angle-right'"></i>
                                <!-- </button> -->
                            </span>
                        </h6>
                        
                         <ul v-if="sidenav.subheadings.length > 0" :class="sidenav.id == current_sidenav_id ? 'open-it animated slideInDown' : 'close-it'" class="list-group list-group-inner">
                            <li v-for="subheading in sidenav.subheadings" :key="subheading.id" class="list-group-item inner-list-item">
                                <a :href="stripSpace(subheading.name)"> {{subheading.name}} </a>
                            </li>
                        </ul>

                    </li>
                </ul>
            </div>
            <!-- End of side navigation -->
            <div class="col-md-9 content-cont">
                <!-- Header Section -->
                <div class="row">

                </div>
                <!-- End of header section -->

                <!-- Body -->
                <div class="row bundler-row p-0">
                    <div class="col-12">

                        <!-- Loader -->
                        <div class="container-fluid" v-if="page_object.markdown_text.length <= 0">
                            <div class="row">
                                <div class="col-12">
                                    <div class="loader">
                                        <img class="img-fluid" :src="loader_url" alt="">
                                    </div>
                                </div>
                            </div>

                            <div class="row">
                                <div class="col-12 text-center">
                                    <h6>Please Wait. Loading Page Data....</h6>
                                </div>
                            </div>
                        </div>
                        <!-- End of loader -->

                        <HeaderPoints v-if="page_object.markdown_text.length > 0" :section="page_object.section" :markdown_text="page_object.markdown_text" :sidenav="page_object.sidenav" />
                    </div>
                </div>
                <!-- End of The body -->

                <!-- Helpfull ? -->
                <div class="row mb-2" v-if="page_object.markdown_text.length > 0">
                    <div class="col-12">
                        <div class="helpfull pt-3 pb-3">
                            <div class="container-fluid">

                                <div class="row">
                                    <div class="col-12 text-center">
                                        <h3>Was this page Helpful?</h3>
                                    </div>
                                </div>

                                <div class="row pt-4">
                                    <div class="col-12 text-center">
                                        <button class="btn yes" @click="feedbackform(true)">Yes</button>
                                        <button class="btn no" @click="feedbackform(false)">No</button>
                                    </div>
                                </div>

                            <!-- Form -->
                                <FeedBackForm v-on:success-submit="successSubmit" v-if="showfeedbackform" :selections="selections" />
                            <!-- End of Form -->

                            <div class="container-fluid">
                                <div class="row">
                                    <div class="col-12">
                                        <p v-if="submit_success" class="success-submit lead py-4 text-center"> Thank You For Your Response. <span v-if="not_positive"> We are Working On it.</span> </p>
                                    </div>
                                </div>
                            </div>

                            </div>
                        </div>
                    </div>
                </div>
                <!-- End of Helpfull ? -->
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { Config } from '../config.js'
import HeaderPoints from './HeaderPoints'
import FeedBackForm from './FeedbackForm'


export default {
    components: {
        HeaderPoints,
        FeedBackForm
    }, 
    data() {
        return {
            page_object: {
                section: null,
                sidenav: null,
                markdown_text: "",
                sidenavs: []
            },
            current_sidenav_id: 0,
            selections: false,
            showfeedbackform: false,
            submit_success: false,
            not_positive: null,
            show_side_nav: false,
            loader_url: "https://i.stack.imgur.com/Ndsyl.gif"  
        }
    },
    methods: {
        updateCurrentObject(object) {
            if(object.pages != null) {
                this.page_object.markdown_text = object.pages.text
                this.page_object.sidenav = object.pages.sidenav
                this.page_object.section = object.pages.section
                this.current_sidenav_id = object.id

                // disable side nav
                this.show_side_nav = false

            } else {

                // disable side nav in the right context
                this.show_side_nav = false
                
                alert("No Content Avalable")
            }
        },
        stripSpace(text) {
            let new_text = text.toLowerCase()
            return `#${new_text.split(' ').join('').split("(").join("").split(")").join("")}`
        },
        setCurrentId(id) {
            alert(id)
        },
        feedbackform(bool) {
            if(bool) {
                this.showfeedbackform = !this.showfeedbackform
                this.submit_success = false
                this.selections = false
            } else {
                this.showfeedbackform = !this.showfeedbackform
                this.submit_success = false
                this.selections = true
            }
        },
        successSubmit(response) {
            this.showfeedbackform = false
            this.submit_success = true
            this.not_positive = response
        }
    },
    created() {
        axios.get(`${Config.rootUrl}/sidenavs`)
        .then((resp) => {
            this.page_object.sidenavs = resp.data

            let first_item = this.page_object.sidenavs[0]
            this.updateCurrentObject(first_item)

        })
        .catch(err => alert(err))
    }
}
</script>

<style scoped>

    .loader {
        z-index: 200;
        margin: auto;
        width: 40%;
        padding-top: 150px;
    }

    .loader img {
        width: 100%;
        height: 100%;
    }

    .side-nav-cont {
        border-top: 1.5px solid rgb(154,211,228);
        padding-top: 100px !important;
        padding-bottom: 100px !important;
        overflow-y: scroll;
        height: 100vh;
        background: white;
    }

    #side-nav-cont::-webkit-scrollbar {
        width: 2px;
    }

    #side-nav-cont::-webkit-scrollbar-thumb {
        background-color: rgb(23,99,173);
        width: 20% !important;
        margin: auto;
    }

    .list-group-item {
        border: none;
        color: rgb(23,99,173);
        transition-duration: .2s;
    }

    .list-group-item span {
        float: right;
    }

    .current-indicate {
        background: rgb(23,99,173);
        width: 5px;
        height: 5px;
        display: inline-block;
        border-radius: 50%;
        margin-top: 1px !important;
    }

    .list-group-item:hover {
        cursor: pointer;
    }

    .p-size {
        transition-duration: .3s;
        padding-top: 7px;
        padding-bottom: 7px;
    }

    .p-size:hover {
        background: rgb(180, 213, 224);
    }

    .drop-subheadings-btn {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 0;
        outline: 0;
        color: inherit;
        background: transparent;
        transition-duration: .5s;
    }

    .drop-subheadings-btn:hover {
        transform: rotate(90deg);
        cursor: crosshair;
    }

    .drop-subheadings-btn:hover i {
        transform: scale(1.5)
    }

    .list-group-inner {
        overflow: hidden;
        transition-duration: .5s;
    }

    .open-it {
        height: auto;
        animation-name: open-it;
        animation-duration: .5s;
        animation-fill-mode: forwards;
    }

    .close-it {
        height: 0;
        animation-name: close-it;
        animation-duration: 1s;
        animation-fill-mode: forwards;
    }

    .inner-list-item {
        color: rgb(155,212,229);
        transition-duration: .2s;
        background: transparent;
        font-size: 14px;
    }

    .inner-list-item a {
        text-decoration: none;
    }

    .content-cont {
        border-top: 1.5px solid rgb(154,211,228);
        /* border-left: 1.5px solid rgb(154,211,228); */
    }

    .bundler-row {
        background: rgb(249,252,253);
    }

    .list-group-inner {
        overflow: hidden;
        transition-duration: .5s;
    }

    .helpfull {
        width: 60%;
        margin: auto;
        background: rgb(0,84,166);
        margin-top: 5px;
        border-radius: 5px;
        color: white;
    }

    .yes {
        background: rgb(154,211,228);
        color: white;
        margin-right: 5px;
        width: 90px;
    }

    .no { 
        background: rgb(154,211,228);
        color: white;
        width: 90px;
    }

    .feed-btn-active {
        border: none;
        outline: none;
        background: rgb(0,84,166);
    }

    .success-submit {
        color: rgb(154,211,228) !important;
    }




    /* Responsive Side Nav */
    @media screen and (max-width: 767px) {

        .helpfull {
            width: 100%;
        }

        .side-nav-cont {
            display: none;
            padding-top: 0px !important;
            padding-bottom: 0px !important;
        }

        .side-nav-cont-show {
            display: block;
            position: fixed;
            top: 50px;
            border-top: none;
            z-index: 2;
            width: 60%;
            box-shadow: 5px 10px 5px #888888;
        }

        .arrow-cont {
            display: block !important;
            width: 50px;
            height: 50px;
            border-radius: 50%;
            position: fixed;
            z-index: 3;
            right: 41%;
            background: rgb(0,84,166);
            color: white;
            border: none;
            outline: none;
        }

        .arrow-cont-open {
            display: block;
            left: 2px;
            top: 130px;
        }

        .hide {
            display: none !important;
        }
    }

    .arrow-cont {
        display: none;
    }

    
</style>