import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Retailer from './views/Retailer.vue'
import AtaGlance from './views/AtaGlance.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/retailer',
            name: 'retailer',
            component: Retailer
        },
        {
            path: '/ataglance',
            name: 'ataglance',
            component: AtaGlance
        },
        {
            path: '/about',
            name: 'about',

            component: () =>
                import ('./views/About.vue')
        }
    ]
})