<template>
    <div class="container-fluid">

        <!-- Bunner Row -->
        <div class="row">
            <div class="col-12 p-0">
                <Bunner v-bind:msg="msg" />
            </div>
        </div>
        <!-- End of bunner Row -->

        <!-- Form Row -->
        <div class="row p-0">
            <div class="col-12">
                <SingleForm />
            </div>
        </div>
        <!-- End Form Row -->

        <!-- At a glance -->
        <div class="row pt-5 pb-5">
            <div class="col-12 p-0">
                <div v-if="sidenavs.length > 0" class="sidenavs-cont">
                    <div v-for="sidenav in sidenavs" :key="sidenav.id" class="sidenav-item">
                        <router-link to="#" :class="sidenav.description == null ? 'no-desk': 'item'">
                            <h2> {{ sidenav.name }} </h2>

                            <p v-if="sidenav.description != null" class="lead"> {{ sidenav.description }} </p>

                        </router-link>
                     </div>
                </div>
            </div>
        </div>
        <!-- End of At a glance -->


        <!-- Footer -->
        <div class="row">
            <div class="col-12 p-0">
                <Footer />
            </div>
        </div>
        <!-- End of Footer -->
    </div>
</template>

<script>
import axios from 'axios'
import Bunner from '../components/Bunner'
import SingleForm from '../components/SingleForm'
import Footer from '../components/Footer'
import { Config } from '../config.js'

export default {
    components: {
        Bunner,
        SingleForm,
        Footer
    },
    data() {
        return {
            msg: "Welcome Back Retailer",
            sidenavs: null
        }
    },
    created() {
        axios.get(`${Config.rootUrl}/sidenavs`)
        .then((resp) => {
            this.sidenavs = resp.data
            // console.log(resp.data)
        })
        .catch(err => alert(err))
    }
}
</script>

<style scoped>

    .sidenavs-cont {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: auto;
    }

    .sidenav-item-null {
        flex: 1;
        flex-basis: 50%;
    }

    .sidenav-item {
        flex: 1;
        flex-basis: 50%;
    }

    .no-desk {
        display: block;
        text-decoration: none;
        width: 70%;
        margin: auto;
        padding-top: 90px;
        padding-bottom: 90px;
        border: 1px solid rgb(26,101,175);
        text-align: center;
        margin-top: 5px;
        border-radius: 5px;
        color: rgb(26,101,175);
        transition-duration: .5s;
    }

    .item {
        display: block;
        text-decoration: none;
        width: 70%;
        margin: auto;
        padding-top: 30px !important;
        padding-bottom: 30px !important;
        border: 1px solid rgb(26,101,175);
        text-align: center;
        margin-top: 5px;
        border-radius: 5px;
        padding: 5px; 
        color: rgb(26,101,175);
        transition-duration: .5s;
    }

    .no-desk:hover {
        color: rgb(56,170,204);
       background: rgb(249,252,253);
       transform: scale(.96);
    }

    .item:hover {
        color: rgb(56,170,204);
       background: rgb(249,252,253);
       transform: scale(.96);
    }
</style>