<template>
    <div class="container-fluid p-0">
        <div class="row">
            <div class="col-12 content-head pt-3 pb-3">
                <strong class="strong">
                    Retailpay Help Center   /   {{ section_text }} /   <span>{{ sidenav }}</span>
                </strong>
            </div>
        </div>

        <div class="row pb-5 pt-5 content-cont">
            <div v-html="htmlContent" class="col-12 content" id="content"></div>
        </div>
    </div>
</template>


<script>
import showdown from "showdown";
import { PassThrough } from 'stream';

export default {
    data() {
        return {
            section_text: "Unknown",
            htmlContent: null,
            sidenav_text: null
        }
    },
    props: ["markdown_text", "sidenav", "section"],
    methods: {
        update() {
            let converter = new showdown.Converter(), text = this.markdown_text
            this.htmlContent = converter.makeHtml(text);
        }
    },
    watch: {
        markdown_text: {
            immediate: true,
            handler() {
                this.update()
            }
        },
        section: {
            immediate: true,
            handler(newvalue) {
                if(newvalue) {
                    this.section_text = newvalue;
                } else {
                    this.section_text = "Unknown"
                }
                
            }
        }
    }
}
</script>


<style>
    .content-head {
        background: white;
        padding-left: 50px;
    }
    .strong {
        color: rgb(0,84,166)
    }

    .strong span {
        color: gray;
        font-weight: lighter;
    }

    .header {
        font-size: 30px;
        color: rgb(0,84,166)
    }

    .subheading {
        color: rgb(0,84,166);
    }

    .content-text {
        color: rgb(155,212,229);
    }

    .video-cont {
        background: gray;
        width: 70%;
        height: 350px;
    }

    .content {
        padding-left: 50px;        
    }

    .content {
        overflow-y: scroll;
        height: 100vh;
    }

    .content::-webkit-scrollbar {
        /* background: red; */
        width: 3px;
    }

    .content::-webkit-scrollbar-thumb {
        background: rgb(23,99,173);
    }

    h1  {
        color: rgb(23,99,173);
    }

    #content h2  {
        color: rgb(23,99,173);
    }

    #content h4 {
       color: rgb(23,99,173);
       margin-top: 20px !important; 
       margin-bottom: 20px !important; 
    }

    #content h5 {
        color: rgb(23,99,173);
    }

    #content h6 {
        color: rgb(23,99,173);
        margin-top: 20px !important; 
        margin-bottom: 20px !important; 
    }

    #content p {
        color: gray;
    }

    #content ul {
        list-style: none;
    }

    #content ul {
        color: gray;
    }

    #content ul li::before {
        content: "\2022";
        color: rgb(54,169,204);
        font-weight: bold;
        display: inline-block;
        width: 1em;
        margin-left: -1em;
        font-size: 30px;
    }

    img {
        width: 50%;
    }

</style>