<template>
    <div class="container-fluid">
        <div class="row pt-5">
            <div class="col-12 pb-4" :class="selections ? 'pt-2' : 'pt-0'">
                <form class="form pb-2 animated fadeIn" :class="selections ? 'pt-4' : 'pt-0'">

                    <div class="container-fluid" v-if="questions.length > 0 && selections">
                        <div class="row">
                            <div class="col-12">
                                <div v-for="question in questions" :key="question.id" class="form-check my-2">
                                    <input class="form-check-input" @click="selectQ(question.question, question.reason)" name="question_to_answer" type="radio">
                                    <label class="form-check-label" :for="question.reason"> {{ question.reason }} </label>
                                </div>
                            </div>
                        </div>
                    </div>

                    <input class="form-control form-control-lg mt-2 mb-2" v-model="feedback.answer" type="text" :placeholder="selections ? placeholder : 'Tell Us What You Like About This Page'">

                    <div class="btn-cont my-3">
                        <button type="button" @click="submitData" class="btn btn-custom">Submit</button>
                    </div>

                </form>
            </div>
        </div>
    </div>    
</template>

<script>
import { Config } from "../config.js"
import axios from "axios"

export default {
    data() {
        return {
            feedback: {
                reason: null,
                question: this.placeholder,
                answer: "",
                general: null
            },
            placeholder: null,
            questions: []
            }
    },props: ["selections"],
    methods: {
        submitData() {

            let data = this.feedback

            if(this.selections) {
                if(data.reason == null && data.question == undefined && data.answer == "") {
                    data.answer = "No Answer Provided"
                    data.question = "No Question selected"
                    data.reason = "Other"
                } else if(data.reason != null && data.answer == "") {
                    data.answer = "No Answer Provided"
                } else if(data.reason == null && data.answer != "") {
                    data.reason = "Other"
                }

                data.general = "No"

            } else {
                delete data.question
                delete data.reason
                data.general = "Yes"
            }
            
            axios.post(`${Config.rootUrl}/feedbacks`, data)
            .then((resp) => {
                this.$emit("success-submit", this.selections)
            })
            .catch((error) => {
                alert(error)
            })

        },
        selectQ(question, reason) {
            this.placeholder = question
            this.feedback.reason = reason
            this.feedback.question = question
        }
    },
    created() {

        // Emit an event to make the parent know which for is rendered
        this.$emit("which-form", this.selections)

        axios.get(`${Config.rootUrl}/feedbackquestions`)
        .then((resp) => {
            this.questions = resp.data
        })
        .catch(err => alert(err))
    }
    
}
</script>

<style scoped>
    form {
        width: 80%;
        margin: auto;
    }

    form input[type=radio] {
        transform: scale(1.5);
    }

    form label {
        font-size: 18px;
    }

    form input[type=text] {
        font-size: 18px;
    }

    .btn-cont {
        width: 50%;
        margin: auto;
    }

    .btn-custom {
        width: 100%;
        background:  rgb(154,211,228);
        color: white;
    }

    @media screen and (max-width: 767px) {
        form {
            width: 100%;
        }

        .btn-cont {
            width: 70%;
        }
    }
</style>